* {
  margin: 0;
  padding: 0;
  font-family: Avenir Next, Avenir, Helvetica Neue, Helvetica, Arial, sans-serif;
}

html, body {
  width: 100%;
  height: 100%;
  background: linear-gradient(#badbe4, #aaf7e1);
}

#world {
  width: 75%;
  height: 100%;
  position: absolute;
  right: 0;
  overflow: hidden;
}

.panel {
  width: 25%;
  box-sizing: border-box;
  padding: 2em 3em;
  position: absolute;
  top: 0;
  left: 0;
}

.game-title {
  z-index: 100;
  text-align: center;
  position: relative;
}

.game-title .title {
  font-family: Playfair Display, serif;
  font-size: 3em;
  font-weight: 700;
}

.game-title .byline {
  text-transform: uppercase;
  color: #333;
  letter-spacing: .2em;
  margin-top: .5em;
  font-family: Playfair Display, serif;
  font-size: .8em;
  font-weight: 600;
}

a {
  letter-spacing: .2em;
  font-family: Playfair Display, serif;
  font-weight: 600;
  text-decoration: none;
}

table {
  width: 100%;
  box-sizing: border-box;
  margin-top: 2em;
}

td {
  letter-spacing: .1em;
  text-transform: uppercase;
  font-size: .7em;
  font-weight: 400;
}

td:first-child {
  width: 30%;
  text-align: right;
  padding-right: .5em;
}

td:nth-child(2) {
  width: 70%;
  text-align: left;
  padding-left: .5em;
}

.stat {
  text-align: center;
  margin-top: 2em;
}

.stat p {
  width: 100%;
  text-align: center;
  font-family: Playfair Display;
  font-size: 2em;
}

.stat label {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .25em;
  width: 100%;
  float: left;
  font-size: .8em;
}

#variable-content {
  letter-spacing: .1em;
  text-transform: uppercase;
  text-align: center;
  margin-top: 2em;
  font-size: 1em;
  font-weight: 100;
  line-height: 150%;
}

@keyframes flickerAnimation {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.animate-flicker {
  animation: flickerAnimation 1.5s infinite;
}

